import React, { useState } from "react";
import { currencies } from "../currencies.js";
import { Clock } from "../Clock";
import "./style.css";

export const Form = ({ calculateResult }) => {
  const [currency, setCurrency] = useState(currencies[0].short);
  const [amount, setAmount] = useState("");

  const onSubmit = (event) => {
    event.preventDefault();
    calculateResult(currency, amount);
  }

  return (
    <form className="form" onSubmit={onSubmit}>
      <Clock />
        <fieldset className="form__fieldset">
          <legend className="form__legend">UzupeÅ‚nij dane:</legend>
          <p>
            <label htmlFor="amount" className="form__label">Podaj kwotÄ™ w zÅ‚otÃ³wkach*:</label>
            <input
            value={amount}
            onChange={({target}) => setAmount(target.value)}
            className="form__input"
            type="number" id="amount"
            name="amount"
            required
            />
          </p>
          <p>
            <label htmlFor="currency" className="form__label">Waluta:</label>
            <select
            className="form__input"
            value={currency}
            onChange={({target}) => setCurrency(target.value)}
            name="currency"
            id="currency">
            {currencies.map((currency => (
              <option
                key={currency.short}
                value={currency.short}
                >
                  {currency.name}
                </option>
            )))}
            </select>
          </p>
          <p>
            <button className="form__button">Przelicz</button>
          </p>
        </fieldset>
      </form>
  )

}